<template>
  <div class="container">
    <div class="row">
      <div class="col col-md-4 "></div>

      <!--核心组件 st-->
      <div class="col col-md-4 ">

        <!--图标 st-->
        <div class="text-center" style="padding: 32px 0 24px 0">
          <img src="@/assets/structure/register.png"  style="height: 72px" alt=" ">
        </div>
        <!--图标 ed-->

        <!--欢迎语 st-->
        <div class="text-center">
          <p style="font-size: 24px">欢迎注册 upuper</p>

        </div>
        <!--欢迎语 ed-->

        <!--注册 st-->
        <div style="margin-top: 16px; border:1px solid #d8dee2 ; padding: 20px 20px 20px 20px">

          <!--表单 st-->
          <form @submit.prevent>

            <!--手机号码 st-->
            <div class="form-group">
              <label class="register-text">手机号码：</label>
              <input class="form-control" v-model="userTel">
            </div>
            <!--手机号码 ed-->

            <!--短信验证码 st-->
            <div class="form-group">
              <div class="row">
                <div class="col-md-6">
                  <label class="register-text">短信验证码：</label>
                </div>

                <!--获取验证码 st-->
                <div class="col-md-6" style="text-align: right">
                  <button class="btn btn-link register-text" style="padding-top: 0" @click="getTextCode()">获取验证码</button>
                </div>
                <!--获取验证码 st-->

              </div>
              <input class="form-control" v-model="textVerification">
            </div>
            <!--短信验证码 ed-->

            <!--用户名 st-->
            <div class="form-group">
              <div class="col-md-6" style="padding-left: 0">
                <label class="register-text">用户名：</label>
              </div>
              <input type="text" class="form-control" v-model="userName">
            </div>
            <!--用户名 ed-->

            <!--输入密码 st-->
            <div class="form-group">
              <div class="col-md-6" style="padding-left: 0">
                <label class="register-text">输入密码：</label>
              </div>
              <input type="password" class="form-control" v-model="userPassword">
            </div>
            <!--输入密码 ed-->

            <!--再次输入密码 st-->
            <div class="form-group">
              <div class="col-md-6" style="padding-left: 0">
                <label class="register-text">再次输入密码：</label>
              </div>
              <input type="password" class="form-control" v-model="userPasswordConfirm">
            </div>
            <!--再次输入密码 ed-->

            <!--注册按钮-->
            <button class="btn btn-primary register-text" @click="userRegister()">注册</button>
          </form>
          <!--表单 ed-->

        </div>
        <!--注册 ed-->

      </div>
      <!--核心组件 ed-->

    </div>
  </div>
</template>

<script>/* eslint-disable */
import axios from 'axios'

export default {
  data() {
    return {
      userTel: '', //需要是一个合法的手机号
      textVerification: '',//短信验证码
      userName: '', //用户命名
      userPassword: '', //用户设置密码
      userPasswordConfirm: '', //用户重新输入确认密码
      roleId: -1, //用户角色id(0:默认开发者，1:管理员，2:普通用户)
    }
  },
  components: {},
  methods: {
    /*
    * 用户注册
    * */
    userRegister() {
      /*测试
      console.log("userTel", this.userTel);
      console.log("textVerification", this.textVerification);
      console.log("userName", this.userName);
      console.log("userPassword", this.userPassword);
      console.log("userPasswordConfirm", this.userPasswordConfirm);*/

      //需要检测两次输入密码完全相同  检查手机号是否合法   用户名是否合法
      if(!(this.passwordCheck()&&this.userTelCheck()&&this.userNameCheck())){
        //若有一项不符合 则提前打断 拒绝申请提交
        return
      }

      //数据传输
      axios({
        method: 'post',
        url: 'http://schoolmock.com',
        data: {
          'username': this.username,
          'password': this.userPassword,
          'phoneNumber': this.userTel,
          'roleId': this.roleId,
        }
      }).then((res) => {
        console.log(res.data);
      });
    },
    /*
    * 用户注册
    * */

    /*
    * 用户注册的检查函数
    * */

    /*
    * 1、检查两次密码是否相同
    * */
    passwordCheck() {
      if (this.userPassword !== this.userPasswordConfirm) {
        alert("两次输入密码不相同！");
        return false
      }
      return true
    },

    /*
    * 2、检查手机号是否合法
    * */
    userTelCheck() {
      let rule = /^1\d{10}$/;
      //验证规则：11位数字，以1开头
      if (!rule.test(this.userTel)) {
        alert("请填写正确的11位手机号！");
        return false
      }
      return true
    },

    /*
    * 3、检查用户名是否合法
    * */
    userNameCheck() {
      //验证规则：字母、数字、下划线组成，字母开头，4-16位
      let rule = /^[a-zA-z]\w{3,15}$/;
      if(!rule.test(this.userName)){
        alert("用户名不合法！");
        alert("请使用字母、数字、下划线组成，字母开头的用户名，长度4-16位");
        return false
      }
      return true
    },
    /*
    * 用户注册的检查函数
    * */

    /*
    * 验证码发送
    * */
    getTextCode(){
      console.log("获取短信验证码.....v")
    }
    /*
    * 验证码发送
    * */


  },
}
</script>

<style>
  .register-text {
    font-size: 14px;
  }
</style>



